<template>
  <v-row class="justify-center mt-10">
    <v-col lg="3" md="5" sm="7">
      <v-img
        max-height="250"
        max-width="100%"
        src="@/assets/logo.svg"
        class="mb-10"
        contain
      ></v-img>
      <v-form
        ref="form"
        v-model="valid"
      >
        <v-text-field
          v-model="email"
          :rules="emailRules"
          label="Email"
          required
          outlined
          clearable
          color="primary"
        ></v-text-field>

        <v-text-field
          v-model="password"
          :counter="30"
          :rules="passwordRules"
          label="Password"
          type="password"
          required
          outlined
          clearable
          color="primary"
        ></v-text-field>

        <v-checkbox
          v-model="checkbox"
          label="Auto Login"
        ></v-checkbox>

        <v-dialog
          v-model="dialog"
          persistent
          max-width="290"
        >
          <template v-slot:activator="{ on }">
            <v-btn
              :disabled="!valid"
              color="primary"
              @click="validate"
              x-large
              block
            >
              Login
            </v-btn>
          </template>
          <v-card>
            <v-card-title class="text-h5">
              Incorrect
            </v-card-title>
            <v-card-text>Please check your Login/Password</v-card-text>
            <v-card-actions>

              <v-btn
                color="primary"
                text
                @click="dialog = false"
              >
                Confirm
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-form>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "Login",
  data: () => ({
    valid: true,
    password: '',
    passwordRules: [
      v => !!v || 'password is required',
      v => (v && v.length <= 30) || 'password must be less than 30 characters',
    ],
    email: '',
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
    ],
    checkbox: false,
    dialog: false,
  }),

  methods: {
    validate () {
      this.$refs.form.validate()
      if (this.email === 'suorgx@aiaracorp.com') {
        this.$store.commit('changeAuth')
      } else {
        this.dialog = true
      }
    },
  },
}
</script>

